import { prisma } from "../lib/db";
import { addUser } from "./actions"; // Import the action

export default async function Home() {
  const users = await prisma.user.findMany();

  return (
    <main className="min-h-screen bg-zinc-950 text-white p-12">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-4xl font-black mb-8 text-cyan-400">Guestbook</h1>

        {/* NEW FORM */}
        <form action={addUser} className="mb-12 flex flex-col gap-4 bg-zinc-900 p-6 rounded-2xl border border-zinc-800">
          <input 
            name="name" 
            placeholder="Your Name" 
            className="bg-zinc-800 p-3 rounded-lg border border-zinc-700 outline-none focus:border-cyan-500"
            required 
          />
          <input 
            name="email" 
            type="email" 
            placeholder="Email Address" 
            className="bg-zinc-800 p-3 rounded-lg border border-zinc-700 outline-none focus:border-cyan-500"
            required 
          />
          <button type="submit" className="bg-cyan-600 hover:bg-cyan-500 text-white font-bold py-3 rounded-lg transition-colors">
            Add Me to the List
          </button>
        </form>

        {/* YOUR EXISTING LIST */}
        <div className="grid gap-4">
          {users.map((user) => (
            <div key={user.id} className="p-4 rounded-xl bg-zinc-900/50 border border-zinc-800">
              <p className="font-bold">{user.name}</p>
              <p className="text-zinc-500 text-sm">{user.email}</p>
            </div>
          ))}
        </div>
      </div>
    </main>
  );
}